<template>
  <div>
    <div class="google-map fill-height" ref="googleMap" />
    <va-modal v-model="createModal" no-outside-dismiss
      fullscreen
      hide-default-actions
      :title="$t('generic.Detailes') +' ' + routeName "
      :cancelText=" $t('modal.cancel')">
      <div v-if="loading" class="flex-center spinner-box">
        <SpringSpinner
          :animation-duration="1500"
          :color="this.$themes.primary"
          :size="80"
        >
        </SpringSpinner>
      </div>
      <div  v-else>
        <div class="row justify--space-between">
          <span>{{$t('expectedTime')}}<span>{{expextedTime}}</span></span>
          <span>{{$t('distance')}}<span>{{distance}}</span></span>
          <!-- <span>{{timeNow - expextedTime >= 3 ? '' :''}}</span> -->
          <span>{{$t('timeNow')}} <span>{{timeNow}}</span></span>

        </div>
        <div class="row flex-center">

          <div id="map" class="google-map  fill-height" ref="googleMapDetails" style="height: 69vh;"/>
          <div id="sidebar">
            <div id="panel"></div>
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="createModal = false">
            {{ $t('modal.cancel') }}
          </va-button>
        </div>
      </div>
    </va-modal>
  </div>
</template>

<script>
import * as GoogleMapsLoader from 'google-maps'
import * as spinners from 'epic-spinners'
import axios from 'axios'
import { drawPoly } from '../../../helper/Pahts'

export default {
  name: 'google-map',
  data () {
    return {
      routeName: '',
      timeNow: '',
      distance: '',
      expextedTime: '',
      loading: true,
      createModal: false,
    }
  },
  components: {
    ...spinners,
  },
  mounted () {
    const self = this
    GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
    /* We stick to version updates instead of weekly channel update because
       using weekly updates you need to test them, but we prefer to use stable
       versions. */
    GoogleMapsLoader.VERSION = '3.35'
    GoogleMapsLoader.load((google) => {
      /* eslint-disable no-new */
      const map = new google.maps.Map(this.$refs.googleMap, {
        center: new google.maps.LatLng(24.774265, 46.738586),
        zoom: 11,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      })
      drawPoly(google, map)

      // const trafficLayer = new google.maps.TrafficLayer()
      // trafficLayer.setMap(map)
      var routes = [
        {
          origin: new google.maps.LatLng(24.696446214860174, 46.703075887586145),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.714523273595294, 46.823145416155626),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.817984171382186, 46.59366072235679),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.91405527182876, 46.80472804931016),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.76518785411942, 46.80550539629352),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.687550918593196, 46.81777779788274),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.64447056771104, 46.73802076197436),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        },
        {
          origin: new google.maps.LatLng(24.635849084130076, 46.664953236248834),
          destination: new google.maps.LatLng(24.835745918122562, 46.72987580001744),
        }]

      var directionsService = new google.maps.DirectionsService()
      function getPoly () {
        var polylineOptionsActual = new google.maps.Polyline({
          strokeColor: '#133ea0',
          geodesic: true,
          strokeOpacity: 0.6,
          strokeWeight: 6,
        })
        return polylineOptionsActual
      }
      routes.forEach((route, i) => {
        var po = getPoly()
        var rendererOptions = {
          preserveViewport: true,
          suppressMarkers: true,
          routeIndex: Math.random(),
          polylineOptions: po,
        }
        var request = {
          origin: route.origin,
          destination: route.destination,
          travelMode: google.maps.TravelMode.DRIVING,
        }
        var directionsDisplay = new google.maps.DirectionsRenderer(rendererOptions)
        directionsDisplay.setMap(map)

        directionsService.route(request, function (result, status) {
          console.log(result)
          new google.maps.Marker({
            position: new google.maps.LatLng(route.origin),
            map: map,
          })
          if (status === google.maps.DirectionsStatus.OK) {
            directionsDisplay.setDirections(result)
            google.maps.event.addListener(po, 'click', function (evt) {
              console.log('route' + i)
              self.routeName = 'السليمانية  ' + i
              self.createModal = true
              self.onRouteClick(route, result)
            })
          }
        })
      })
      new google.maps.Marker({
        position: new google.maps.LatLng(routes[0].destination),
        map: map,
        animation: google.maps.Animation.BOUNCE,
        icon: 'https://gcdnb.pbrd.co/images/hnKUay2TdxIv.png',
      })
    })
  },
  methods: {
    onRouteClick (route, result) {
      this.loading = true
      this.createModal = true
      const self = this
      var req = {
        origin: route.origin,
        destination: route.destination,
        waypoints: [],
        departureTime: null,
      }
      axios.post('http://34.78.114.66:9090/routes/computeRouteWithTrafficLength', req).then(res => {
        this.loading = false
        self.timeNow = res.data[0].statistics.duration.text
        self.expextedTime = res.data[0].statistics.staticDuration.text
        self.distance = res.data[0].statistics.distance.text
        GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
        GoogleMapsLoader.VERSION = '3.35'
        GoogleMapsLoader.load((google) => {
          setTimeout(() => {
            const map = new google.maps.Map(this.$refs.googleMapDetails, {
              center: new google.maps.LatLng(24.774265, 46.738586),
              zoom: 11,
              mapTypeId: google.maps.MapTypeId.ROADMAP,
            })
            var colors = { TRAFFIC_JAM: '#d74342', SLOW: '#e69e01', NORMAL: '#4078c9' }
            const decodedPath = res.data[0].polyline.geoJsonLinestring.coordinates
            const decodedPathLatLng = decodedPath.map(elt => { return { lat: elt[1], lng: elt[0] } })
            res.data[0].travelAdvisory.speedReadingIntervals.forEach((step) => {
              const currentPath = decodedPathLatLng.slice(step.startPolylinePointIndex === 0 ? 0 : (step.startPolylinePointIndex - 1), step.endPolylinePointIndex)
              var polylineOptionsActual = new google.maps.Polyline({
                path: currentPath,
                strokeColor: colors[step.speed],
                geodesic: true,
                strokeOpacity: 1,
                strokeWeight: 6,
              })
              polylineOptionsActual.setMap(map)
            })
            const po = new google.maps.Polyline({
              strokeColor: 'transparent',
              geodesic: true,
              strokeOpacity: 0.1,
              strokeWeight: 6,
            })
            const display = new google.maps.DirectionsRenderer({
              preserveViewport: true,
              suppressMarkers: true,
              map,
              polylineOptions: po,
              panel: document.getElementById('panel'),
            })

            display.setDirections(result)

            // eslint-disable-next-line no-new
            new google.maps.Marker({
              position: route.origin,
              map: map,
              animation: google.maps.Animation.BOUNCE,
            })
            // eslint-disable-next-line no-new
            new google.maps.Marker({
              position: route.destination,
              map: map,
              animation: google.maps.Animation.BOUNCE,
            })
          }, 100)
        })
      })
    },
  },
}
</script>
